.container {
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
    // max-width: 1400px;

    @media screen and (min-width: 480px) {
        max-width: 480px;

        @media screen and (min-width: 768px) {
            max-width: 768px;
        }

        @media screen and (min-width: 1200px) {
            max-width: 1200px;
        }
    }
}